import axios from 'axios'
import { useContext, useEffect, useState } from 'react';
import AutuContext from '../Authenti/AutuContext';


const HomePage = () => {
    
    const { isAuthenticated, setIsAuthenticated } = useContext(AutuContext);
    const [module, setModule] = useState([]);
    const fetchModule = () => {
    axios.get("http://67.211.210.18:3000/api/modules").then((response) => {
      const data = response.data;
      setModule(data);
    });
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      fetchModule();
    }, 500);
    return () => clearTimeout(timer);
  }, []);
    return ( 
        <>
            <img className="img-fluid" src="./assets/img/slider-4.jpg" alt=""/>
            <div  style={{position:'absolute', top:"340px",fontFamily:"Roboto Slab"}}>
              <h1  style={{fontSize:"80px"}} className="font-weight-bold text-light ml-3 ttt">منصة تعليمية</h1>
              <p className="text-light ml-5">منصة تعتمد على الذكاء الاصطناعي</p>
              </div>
            <div className=" row">
            <div className=" col-md-12">
            <div className=" p-5 " style={{ backgroundColor:"#2db6a3" }}>
                <div className="col-md-12 ">
                <h2 className="text-center text-light">الاختبارات عن طريق الذكاء الاصطناعي<i className="text-light fas fa-laptop-code"></i></h2>
                    <p className="text-center text-light"> منصة تعتمد على الذكاء الاصطناعي </p>
                </div>
            </div>
              <h1 className="text-center h2 mt-3"><span className="text-warning "> وزارة التربية والتعليم</span> </h1>
              <p className="text-center">برنامج تعليمي و منصة اختبار تعتمد على الذكاء الاصطناعي</p>
            </div>
            <div className=" col-md-12">
            <div className=" row">
            <div className=" col-md-1"></div>
            <div className=" col-md-3">
            </div>
            <div className=" col-md-3">
                <div className=" col-md-1"></div>
              </div>
            </div>


            </div>
            </div>
              

        </>
     );
}
 
export default HomePage;

